*{
    margin:0; padding:0;
    box-sizing: border-box;
    transition: all .2s linear;
  }
body {
    overflow-y: scroll;
    width:99vw;
}
.gpt3__navbar {
    display: flex;
    flex-direction: row;
    height:15vh;
    padding: 1vw 2vh;

}
.milagre {
    display:flex;
    flex-direction: row;
    gap:3vw;
}


.gpt3__navbar-links {
    margin-top:-2vh;
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap:2vw;
}
.gpt3__navbar-links a:hover{
 color: rgb(221, 36, 36);
}

.gpt3__navbar-links_logo {
    margin-top:5.5vh;
    margin-right: 2vw;
}

.gpt3__navbar-links_logo img {
    height:19vh;
}

.gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
}
.contingencias-button button{
        
        background-color: rgba(131, 131, 131, 0.171);
        height:5vh;
        font-size: 2.2vh;
        color:#fff;
        width: 15vw;
        border-radius: 20px;
        border: none;
        margin-top:2vh;
        z-index: 1001;
      

}
.contingencias-button button:hover{
    color:red;
    cursor:pointer;
}
.contingencias-button{
    display:flex;
    flex-direction: column;
    margin-top:0vh;
    align-items: center;
    width:12vw;
    right:43vw;
    top:7.2vh;

}
.contingencias-button a:hover{
    color: #ff2f00;}
.funcaoconting{
    background-color: rgba(73, 71, 71, 0.856);
    margin-top:7vh;
    position:absolute;
    height: 22vh;
    border-radius: 5px;  
    margin-left: 0vw;
}
.itemconting{
    display:flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color:rgb(255, 255, 255);
    width:12vw;
    margin-top: 1vh;
    font-size: 2vh;
}
                                .funcaonome{
                                    background-color: rgba(44, 44, 44, 0.527);
                                    height:17vh;
                                    border-radius: 5px;
                                   
                                }
                                .gpt3__navbar-sign {
                                    display:flex;
                                    flex-direction: column;
                                    top:7vh;
                                    right:6vw;
                                    margin-top:-2vh;
                                    position:absolute;
                                    align-items: center;
                                    z-index: 1001;
                                    width:15vw;
                                }
                                .itemsp{
                                    display:flex;
                                    flex-direction: column;
                                    align-items: center;
                                    text-align: center;
                                    color:white;
                                    width:12vw;
                                    margin-top: 1vh;
                                }

                                .itemsp a:hover{
                                    color: red;
                                }

.gpt3__navbar-links_container p,
.gpt3__navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 2.2vh;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.gpt3__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #ff2f00;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
    position:absolute;
}
.gpt3__navbar-sign button{
    padding: 0.5rem 1rem;
    color: #fff;
    background: #ff2f00;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1.2vw;
    line-height: 3vh;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
}
.gpt3__navbar-menu {
    margin-left: 50vw;

    display: none;
    position: relative;
}

.gpt3__navbar-menu svg {
    cursor: pointer;
}

.gpt3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.gpt3__navbar-menu_container p {
    margin: 0;
}

.gpt3__navbar-menu_container-links-sign {
    display: none;
}




@media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container {
        display: none;
    }
    .contingencias-button{
        display:none;
    }

    .gpt3__navbar-menu {
        display: flex;
        right: 15vw;
        top:2vh;
    }
    .gpt3__navbar-menu_container-links-sign{
        display:flex;
        flex-direction: column;
    }
}

@media screen and (max-width: 700px) {
    .gpt3__navbar {
        padding: 2rem ;
    }
}

@media screen and (max-width: 650px) {
    .gpt3__navbar {
        padding: 1rem;
    }

    .gpt3__navbar-sign {
        display: none;
    }

    .gpt3__navbar-menu_container {
        top: 20px;
    }

    .gpt3__navbar-menu_container-links-sign {
        display: flex;
        margin-right: 40vw;
    }
    .nomes-mobile{
        margin-right:-40vw;
        margin-top:10vh;
    }

}